import { Location } from '@angular/common';
import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Data, Params } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { ToastrService } from 'ngx-toastr';
import { Subject, Subscription, combineLatest } from 'rxjs';
import { FieldDefinition, IFieldDataSource, IFieldOptions, MetaDataType } from 'shared-code-lib';
import { FaxUserStatus, IFaxUserSelect } from '../../blocks/InterFAX/Models/admin';
import { Device, DeviceAuthenticationMode } from '../../blocks/models/device';
import { ErrorsService } from '../../blocks/services/errors.service';
import { HydraApiService } from '../../blocks/services/hydra-api.service';
import { InterFAXService } from '../../blocks/services/interfax.service';
import { ApiResult, Control, User, UserTypes } from '../../blocks/services/models';
import { SpinnerStateService } from '../../blocks/services/spinner-state.service';
import { UserStateService } from '../../blocks/services/user-state.service';

@Component({
  selector: 'app-admin-device',
  templateUrl: './admin-device.component.html',
  styleUrls: ['./admin-device.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class AdminDeviceComponent implements OnInit, OnDestroy {
  rightControls: Array<Control> = [];

  routeParams: Params;
  routeData: Data;

  deviceName: FieldDefinition;
  deviceSerialNumber: FieldDefinition;
  deviceTag: FieldDefinition;
  deviceAuthenticationMode: FieldDefinition;
  fieldUsers: FieldDefinition;

  saveButton: Control;
  cancelButton: Control;

  device: Device;

  get deviceAuthentication(): string {
    return `${this.device.authenticationMode}`;
  }
  set deviceAuthentication(val: string) {
    this.device.authenticationMode = Number(val);
  }

  get genericUser(): string {
    return this.userState.user.userType == UserTypes.InterFAX
      ? this.device.genericUserEmail
      : this.device.genericUserId;
  }

  set genericUser(val: string) {
    if (this.userState.user.userType == UserTypes.InterFAX) {
      this.device.genericUserEmail = val;
      return;
    }

    this.device.genericUserId = val;
  }

  subscriptions = new Subscription();

  constructor(
    private translateService: TranslateService,
    private route: ActivatedRoute,
    private hydraApi: HydraApiService,
    private toastr: ToastrService,
    private errorsService: ErrorsService,
    private spinner: SpinnerStateService,
    private location: Location,
    private userState: UserStateService,
    private interfaxService: InterFAXService
  ) {}

  ngOnInit() {
    this.subscriptions.add(
      combineLatest([this.route.params, this.route.data]).subscribe(([params, data]) => {
        this.routeParams = params;
        this.routeData = data;

        this.createButtons();
        this.createFields();
        this.loadDevice();
      })
    );
  }

  private createButtons() {
    this.saveButton = new Control(this.onSave, 'Save', 'btn btn-primary btn-minwidth', '', '', 'btn', false);
    this.cancelButton = new Control(this.onCancel, 'Cancel', 'btn btn-light btn-minwidth', '', '', 'btn', false);
    this.rightControls.push(this.saveButton);
    this.rightControls.push(this.cancelButton);

    this.translateService.get('Metadata.Save').subscribe((res) => {
      this.saveButton.setTranslation(res);
    });
    this.translateService.get('Metadata.Cancel').subscribe((res) => {
      this.cancelButton.setTranslation(res);
    });
  }

  onSave = () => {
    if (this.device.id) {
      this.hydraApi.updateDevice(this.device, this.deviceSaved, this.deviceSaveFailed);
    } else {
      this.hydraApi.createDevice(this.device, this.deviceSaved, this.deviceSaveFailed);
    }

    this.spinner.showSavingSpinner();
  };

  deviceSaved = (result: ApiResult) => {
    if (result.success) {
      this.toastr.success(this.translateService.instant('admin-device.saved'));
      this.returnToGrid();
    } else {
      this.errorsService.showErrorsModal(result.errors, 'admin-device.validation-errors');
    }

    this.spinner.hide();
  };

  deviceSaveFailed = (err: any) => {
    this.toastr.error(this.translateService.instant('admin-device.save-failed'));
    this.spinner.hide();
  };

  onCancel = () => {
    this.returnToGrid();
  };

  returnToGrid = () => this.location.back();

  private loadDevice = () => {
    if (this.routeData.type === 'properties' || this.routeData.type === 'duplicate') {
      this.hydraApi.getDevice(this.routeParams.deviceid, this.deviceLoaded, this.loadFailed);
      return;
    }

    const device = new Device({
      id: '',
      tenantId: this.routeParams.tenantId ?? '',
      name: '',
      serialNumber: '',
      tag: '',
      authenticationMode: DeviceAuthenticationMode.Generic,
      dateCreatedUTC: new Date(),
      dateModifiedUTC: new Date(),
      enabled: true,
      genericUserId: '',
      genericUserEmail: '',
      enableAutomaticIdentityMapping: false,
    });

    this.deviceLoaded(device);
  };

  private deviceLoaded = (d: Device) => {
    if (this.routeData.type === 'duplicate') {
      d.name = `${this.translateService.instant('Admin.CopyOf')} ${d.name}`;
      d.id = '';
    }

    this.device = d;
  };

  deviceAuthenticationModeChanged = (value: string): void => {
    const authenticationMode = parseInt(value);
  };

  private createFields = () => {
    this.deviceName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-device.name'),
      placeholder: this.translateService.instant('admin-device.name'),
      required: true,
    });

    this.deviceSerialNumber = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-device.serial-number'),
      placeholder: this.translateService.instant('admin-device.serial-number'),
      required: true,
    });

    this.deviceTag = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-device.tag'),
      placeholder: this.translateService.instant('admin-device.tag'),
    });

    this.deviceAuthenticationMode = new FieldDefinition({
      type: MetaDataType.List,
      label: this.translateService.instant('admin-device.authentication-mode'),
      placeholder: this.translateService.instant('admin-device.authentication-mode'),
      required: true,
      datasource: new DeviceAuthenticationModeDataSource(this.translateService),
    });

    this.fieldUsers = new FieldDefinition({
      type: MetaDataType.List,
      label: '',
      placeholder: '',
      required: true,
      datasource: new UsersDataSource(this.hydraApi, this.userState, this.interfaxService, this.routeParams?.tenantId),
    });
  };

  private loadFailed = () => {};

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}

export class UsersDataSource implements IFieldDataSource {
  changed = new Subject<void>();

  usersLoaded = (users: Array<User>, successCallBack: (items: IFieldOptions[]) => void): void => {
    const fo = users.map<IFieldOptions>((u) => {
      return { name: u.name, value: u.id };
    });

    successCallBack(fo);
  };

  userLoadFailed = (): void => {};

  constructor(
    private hydraApi: HydraApiService,
    private userState: UserStateService,
    private interfaxService: InterFAXService,
    private tenantId: string = ''
  ) {}

  getItems(
    startsWith: string,
    currentValue: string,
    successCallBack: (items: IFieldOptions[]) => void,
    failureCallBack: (err: any) => void
  ): void {
    if (!this.tenantId) this.tenantId = this.userState.user.tenantId;

    if (this.userState.user.userType == UserTypes.InterFAX) {
      this.interfaxService.getUsersSelect(
        (users: Array<IFaxUserSelect>) => {
          const allUsers: Array<User> = users.map((x) => {
            const user: User = new User();
            user.id = user.name = x.userId;
            return user;
          });
          this.usersLoaded(allUsers, successCallBack);
        },
        () => {},
        startsWith,
        FaxUserStatus.Open
      );

      return;
    }

    this.hydraApi.getUsersByTenantId(
      this.tenantId,
      (users: Array<User>) => {
        this.usersLoaded(users, successCallBack);
      },
      failureCallBack
    );
  }
}

class DeviceAuthenticationModeDataSource implements IFieldDataSource {
  changed = new Subject<void>();

  types: IFieldOptions[] = [
    { value: '0', name: this.translate.instant('admin-device.generic-mode') },
    { value: '1', name: this.translate.instant('admin-device.user-mode') },
    { value: '2', name: this.translate.instant('admin-device.product-auth-mode') },
  ];

  constructor(private translate: TranslateService) {
    this.types.sort((x, y) => x.name.localeCompare(y.name));
  }

  getItems(
    startsWith: string,
    currentValue: string,
    successCallBack: (items: IFieldOptions[]) => void,
    failureCallBack: (err: any) => void
  ): void {
    successCallBack(this.types);
  }
}
